<template>
    <el-radio-group class="ml-4">
        <el-radio label="file" size="large">文件</el-radio>
        <el-radio label="project" size="large">项目</el-radio>
        <el-radio label="object" size="large"> 其它 </el-radio>
    </el-radio-group>
</template>

<script>
export default {

    name: "settings-intent-templates-content-type-radio-group",

    methods: {

        computeKindByContentType(ct) {
            let s = "" + ct;
            if (s.indexOf("project/") == 0) {
                return 'project'
            }
            let i = s.indexOf("/")
            if (i >= 0) {
                return 'file'
            }
            return 'object'
        },

        setValue(value) {
            this.$emit('update:modelValue', value)
        }
    },

    mounted() {
        let value2 = this.contenttype;
        this.setValue(value2)
    },

    props: {
        contenttype: String,
    },

    watch: {
        contenttype(value1) {
            let value2 = this.computeKindByContentType(value1)
            this.setValue(value2)
        },
    },
}

</script>
