<template>
  <el-dialog title="命令模板详情" align-center width="800px">

    <div class="info-parent">

      <MyIcon class="info-icon" :src="item.icon" :size="99" title="" />

      <el-form class="info-form the-item-detail-dialog" label-width="128px">
        <el-form-item label="标题"> {{ item.title }} </el-form-item>
        <el-form-item label="名称"> {{ item.name }} </el-form-item>

        <el-form-item label="ID / UUID"> {{ item.id }} / {{ item.uuid }} </el-form-item>
        <el-form-item label="分组"> {{ item.group }} </el-form-item>
        <el-form-item label="选择表达式"> {{ item.selector }} </el-form-item>

        <el-form-item label="描述"> {{ item.description }} </el-form-item>

        <el-form-item label="命令"> {{ item.command }} </el-form-item>
        <el-form-item label="参数"> {{ item.args }} </el-form-item>
        <el-form-item label="环境变量"> {{ item.env }} </el-form-item>
        <el-form-item label="工作目录"> {{ item.wd }} </el-form-item>

        <el-form-item label="创建时间"> {{ formatTime(item.created_at) }} </el-form-item>
        <el-form-item label="修改时间"> {{ formatTime(item.updated_at) }} </el-form-item>
      </el-form>
    </div>

    <div>
      <ElButton type="default" @click="duplicate">创建副本</ElButton>
      <ElButton type="default" @click="close">关闭</ElButton>
    </div>
  </el-dialog>
</template>

<script>
import { ElButton } from "element-plus";
import MyIcon from '@/components/utils/icon.vue'
import TimeJS from '@/js/time.js'

export default {
  name: "settings-intent-templates-item-detail-dialog",

  components: { ElButton, MyIcon },

  computed: {
    item() {
      let o = this.target;
      if (o == null) {
        o = this.emptyItem;
      }
      return o;
    },
  },

  data() {
    return {
      emptyItem: {},
    };
  },

  methods: {
    close() {
      this.$emit("update:modelValue", false);
    },

    duplicate() {
      let item = this.item;
      this.$store.dispatch("intent_template/duplicate", { item }).then(() => {
        this.close();
      });
    },

    formatTime(ms) {
      return TimeJS.StringifyTimestamp(ms, 'datetime')
    },
  },

  props: {
    target: Object,
  },
};
</script>

<style scoped>
.the-item-detail-dialog {
  text-align: center;
}

.info-parent {
  display: flex;
  flex-direction: row;
}

.info-icon {
  flex-grow: 0;
}

.info-form {
  flex-grow: 1;
}
</style>
